<template>
  <div v-if="cart.length">
    <h2>–°–ø–∏—Å–æ–∫ –ø—Ä–æ–¥—É–∫—Ç–æ–≤</h2>
    <div class="cart-list">
      <div v-for="item in products" :key="item.id">
        {{ item.title }} : {{ item.amount }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
  computed: {
    ...mapState(["cart"]),
    // –ê–Ω–≥–ª–æ—è–∑—ã—á–Ω–æ–µ –∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π
    products() {
      return this.cart.sort((a, b) => {
        return b.date - a.date;
      });
    },
  },
};
</script>

